<template>
  <div class="container">
    <form class="container-caeser" @submit.prevent="submitForm">
      <h2 class="title">Comparación de cifrados</h2>

      <div class="form-group">
        <label for="message" class="form-label"> Mensaje a cifrar: </label>
        <input
          v-model="inputMessage"
          id="message"
          type="text"
          autocomplete="off"
          placeholder="Texto a cifrar"
          class="form-control"
          :class="{
            'form-error': $v.inputMessage.$invalid && inputMessage.length,
          }"
        />
        <span
          :class="{ 'text-error': $v.inputMessage.$invalid }"
          v-show="$v.inputMessage.$invalid && inputMessage.length"
        >
          Solo se permiten letras (A-Z y a-z)
        </span>
      </div>

      <div class="form-group">
        <label for="shift" class="form-label">
          Desplazamiento (César): {{ shift }}
        </label>
        <input
          v-model.number="shift"
          type="range"
          min="1"
          max="25"
          id="shift"
          class="form-slider"
        />
      </div>
      <div class="form-group">
        <label for="shift" class="form-label">
          No. de columnas (Escítala): {{ columns }}
        </label>
        <input
          v-model="columns"
          type="range"
          min="1"
          :max="inputMessage.length"
          id="shift"
          class="form-slider"
        />
      </div>

      <div class="form-group btn-group d-grid">
        <button
          @click="compareCiphers"
          class="btn btn-primary"
          :class="{ disabled: $v.inputMessage.$invalid }"
          :disabled="$v.inputMessage.$invalid"
        >
          Comparar
        </button>
      </div>
    </form>

    <div
      class="container-wrapper"
      v-show="outputMessageCaesar && !$v.inputMessage.$error"
    >
      <div class="container-answer">
        <h3 class="title">Cifrado César o desplazamiento.</h3>
        <p class="text-result text-bold">
          Mensaje sin cifrar:
          <span class="text-normal">{{ inputMessage }}</span>
        </p>
        <p class="text-result text-bold">
          Mensaje cifrado:
          <span class="text-normal">{{ outputMessageCaesar }}</span>
        </p>
        <p class="text-result text-bold">
          Desplazamiento: <span class="text-normal">{{ shift }}</span>
        </p>
      </div>

      <div class="container-answer bg-blue">
        <h3 class="title text-blue-dark">Descripción del cifrado.</h3>
        <p class="text-body text-blue-dark description">
          El cifrado César es un método de sustitución que desplaza las letras
          del alfabeto por un número fijo de posiciones. Es extremadamente
          inseguro, ya que puede romperse fácilmente con un ataque de fuerza
          bruta, probando los 25 posibles desplazamientos, o mediante un
          análisis de frecuencia. Aunque es sencillo de implementar, su
          seguridad es prácticamente nula en contextos moderno
        </p>
      </div>
    </div>

    <div
      class="container-wrapper"
      v-show="outputMessageCaesar && !$v.inputMessage.$error"
    >
      <div v-show="outputMessageEscitala" class="container-answer">
        <h3 class="title">Cifrado de Escítala.</h3>
        <p class="text-result text-bold">
          Mensaje sin cifrar:
          <span class="text-normal">{{ inputMessage }}</span>
        </p>
        <p class="text-result text-bold">
          Mensaje cifrado:
          <span class="text-normal">{{ outputMessageEscitala }}</span>
        </p>
        <p class="text-result text-bold">
          Columnas: <span class="text-normal">{{ columns }}</span>
        </p>
      </div>

      <div class="container-answer bg-yellow">
        <h3 class="title text-yellow-dark">Descripción del cifrado.</h3>
        <p class="text-body text-yellow-dark description">
          El cifrado Escítala es una técnica de transposición utilizada en la
          antigua Grecia, que requería una tira de cuero enrollada en un bastón.
          Su seguridad depende del grosor del bastón, pero es vulnerable a
          deducción de patrones y análisis de transposición. Aunque más complejo
          que el César, también es inseguro y obsoleto frente a las técnicas
          criptográficas actuales.
        </p>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";

const inputMessage = ref("");
const outputMessageCaesar = ref("");
const outputMessageEscitala = ref("");
const shift = ref(1);

//Escilata
const columns = ref(1);

const alphaWithSpaces = (value) => {
  return /^[a-zA-Z\s]*$/.test(value); // Permite letras y espacios
};

const rules = {
  inputMessage: {
    required,
    alphaWithSpaces,
  },
};

const $v = useVuelidate(rules, { inputMessage });

const submitForm = async () => {
  if ($v.value.$invalid) {
    $v.value.$touch();
    return;
  }
};

const compareCiphers = () => {
  encryptCaesar();
  encryptEscitala();
};

// Función para cifrar
const encryptCaesar = () => {
  outputMessageCaesar.value = caesarCipher(inputMessage.value, shift.value);
};

// Lógica del cifrado César
const caesarCipher = (text, shift) => {
  const alphabet = "abcdefghijklmnopqrstuvwxyz";
  return text
    .split("")
    .map((char) => {
      const isUpperCase = char === char.toUpperCase();
      const lowerChar = char.toLowerCase();
      const index = alphabet.indexOf(lowerChar);
      if (index === -1) {
        return char; // No se altera si no es una letra
      }
      let newIndex = (index + shift) % alphabet.length;
      if (newIndex < 0) newIndex = alphabet.length + newIndex;
      const shiftedChar = alphabet[newIndex];
      return isUpperCase ? shiftedChar.toUpperCase() : shiftedChar;
    })
    .join("");
};

// Lógica del cifrado César
const encryptEscitala = () => {
  const rows = Math.ceil(inputMessage.value.length / columns.value);
  let encryptedMessage = "";

  // Recorremos las columnas y construimos el mensaje cifrado
  for (let col = 0; col < columns.value; col++) {
    for (let row = 0; row < rows; row++) {
      const index = row * columns.value + col;
      if (index < inputMessage.value.length) {
        encryptedMessage += inputMessage.value[index];
      }
    }
  }

  outputMessageEscitala.value = encryptedMessage;
};
</script>

<style scoped>
.d-grid {
  display: grid;
  grid-template-columns: 1fr;
}

.container-answer > h3 {
  text-align: center !important;
}

.text-bold {
  font-weight: 700;
  font-family: "Lora";
}

.text-normal {
  font-weight: 400;
}

.text-body {
  line-height: 2rem;
}

.container-wrapper {
  display: grid;
  gap: 2rem;
  align-self: start;
}

.bg-blue {
  background-color: #abb9fa;
}
.text-blue-dark {
  color: #000d47;
}

.bg-yellow {
  background-color: #fad59b;
}
.text-yellow-dark {
  color: #472d00;
}

.description {
  line-height: 3rem;
}
</style>
